<template>
  <vue-picture-cut ref="pictureCut" :src="imgSrc">
    <vue-picture-cut-menu
      slot="menu"
      confirm-name=""
      cancel-name=""
      size-auto-name=""
      size-raw-name=""
      menu-rotate-name=""
      @on-change="saveFileImg"
      @on-cancel="handleClose"
    />
  </vue-picture-cut>
</template>
<script>
import { VuePictureCut, VuePictureCutMenu } from "vue-picture-cut";

export default {
  name: "CropImage",
  components: {
    VuePictureCut,
    VuePictureCutMenu,
  },
  props: {
    imgSrc: {
      type: String,
      default: "",
    },
  },
  data() {
    return {};
  },
  methods: {
    saveFileImg(res) {
      this.$emit("onChange", res.blob, res.base64);
      this.$dialog?.close();
    },
    // 
    handleClose() {
      this.$emit("onCancel");
      this.$dialog?.close();
    },
  },
};
</script>
