<template>
  <div class="ds-control ds-datagrid mobile ds-no-padding" style="height:100%">
    <div class="ds-datagrid-search">
      <van-search v-model="searchStr" shape="round" :placeholder="searchPlaceholder" />
    </div>
    <div class="ds-datagrid-wrap" :style="{'padding':$padding}" v-mobile-scroll>
      <template v-if="isDesign">
        <div class="ds-datagrid-item" :style="{'border-radius':$borderRadius}">
          <slot name="data-item">
            <DsfMobilGridLayout :areas="areas" :is-design="isDesign" :design-only-slot="true" :owner="_self" :grid-rows="contentLayout.heights" :grid-columns="contentLayout.widths" :grid-template="matrix">
              <template v-for="area in areas" v-slot:[area.name]>
                <div :key="area.name" class="grid-area-content" :class="area.setting.class" :style="{ 'padding':area.setting.padding,'height': area.setting.height}">
                  <div class="grid-area-content-cell" :slot-name="area.name">
                    <slot :name="area.name">
                    </slot>
                  </div>
                </div>
              </template>
            </DsfMobilGridLayout>
            <div class="ds-datagrid-item-footer" v-if="isButtonBar" :style="rowFooterStyle()">
              <slot name="row-footer">
                <span class="ds-datagrid-item-footer-title" v-if="rowButtons.length<=0"></span>
                <DsfMobileButtonBar size="small" :align="rowButtonsAlign" :more-button-text="rowButtonsMoreButtonText" :more-button-style="rowButtonsMoreButtonStyle" :max-number="rowButtonsMaxNumber" :buttons="rowButtons"></DsfMobileButtonBar>
              </slot>
            </div>
            <div slot-name="data-item-hidden" class="data-item-hidden">
              <slot name="data-item-hidden">
                <span>()</span>
              </slot>
            </div>
          </slot>
        </div>
        <div v-show="rangeWrap.show" :style="{height:rangeWrap.height+'px',width:rangeWrap.width+'px',top:rangeWrap.top+'px',left:rangeWrap.left+'px'}" class="selected-range"></div>
      </template>
      <template v-else>
        <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
          <van-list v-model="loading" :class="[splitWidthHorizontal]" :finished="finished" :finished-text="finishedText" @load="onLoad">
            <div @click="itemClickHandler(row,$event)" v-for="(row,$index) in data" :key="row._id||dsf.uuid()" class="ds-datagrid-item" :style="{'border-radius':$borderRadius}">
              <slot name="data-item" v-bind="{'row':row,'$index':$index,'_self':_self}">
                <DsfMobilGridLayout :areas="areas" :is-design="isDesign" :design-only-slot="true" :grid-rows="contentLayout.heights" :grid-columns="contentLayout.widths" :grid-template="matrix">
                  <template v-for="area in areas" v-slot:[area.name]>
                    <div :key="area.name" class="grid-area-content" :class="area.setting.class" :style="{'over-flow':'hidden','color':area.setting.color,'padding':area.setting.padding,'height': area.setting.height}">
                      <div class="grid-area-content-cell">
                        <div class="grid-area-conent-wrap" :class="['ds-multi-ellipsis-'+area.setting.lineNum]" :style="{'max-height':area.setting.height,'line-height':area.setting.lineHeight}">
                          <slot :name="area.name" v-bind="{'row':row,'$index':$index,'_self':_self}">
                          </slot>
                        </div>
                      </div>
                    </div>
                  </template>
                </DsfMobilGridLayout>
                <div class="ds-datagrid-item-footer" v-if="isButtonBar" :style="rowFooterStyle()">
                  <slot name="row-footer" v-bind="{'row':row,'$index':$index,'_self':_self}">
                    <DsfMobileButtonBar size="small" :local="row" emit-prefix="row-button" :more-button-text="rowButtonsMoreButtonText" :more-button-style="rowButtonsMoreButtonStyle" :max-number="rowButtonsMaxNumber" :align="rowButtonsAlign" :buttons="rowButtons"></DsfMobileButtonBar>
                  </slot>
                </div>
              </slot>
            </div>
          </van-list>
        </van-pull-refresh>
      </template>
    </div>
  </div>
</template>
<script>
import layout from "_platform/mixins/layout";
import DsfMobilGridLayout from "./mobileDataGridItemLayout";
const GRID_AREA_KEY = "grid-area-";
export default dsf.component({
  name: "DsfMobileDataGrid",
  ctrlCaption: "",
  mixins: [layout],
  components: { DsfMobilGridLayout },
  provide() {
    return {
      $list: this,
      $buttonOwner: this
    };
  },
  data() {
    return {
      rangeWrap: {
        show: false,
        width: 0,
        height: 0,
        top: 0,
        left: 0,
        selected: []
      },
      loading: false,
      finished: false,
      refreshing: false,
      currentPage: 0,
      matrix: null,
      searchStr: ""
    };
  },
  props: {
    searchPlaceholder: {
      type: String,
      default: ""
    },
    finishedText: {
      type: String,
      default: ""
    },
    searchInfo: {
      type: Array,
      default() {
        return [];
      }
    },
    slots: {
      type: Array,
      default() {
        return [
          {
            name: "default",
            controls: []
          },
          {
            name: "data-item",
            controls: [],
            scope: "scope",
            dataKey: "row",
            repeat: true
          },
          {
            name: "data-item-hidden",
            controls: [],
            scope: "scope",
            dataKey: "row",
            repeat: true
          }
        ];
      }
    },
    contentLayout: {
      type: Object,
      default() {
        return {
          grid: [],
          widths: [],
          heights: [],
          setting: {}
        };
      }
    },
    pageSize: {
      type: Number,
      default: 15
    },
    browseScope: {
      type: Object,
      default() {
        return {
          scope: "2",
          unit: []
        };
      }
    },
    cellSerial: {
      type: Number,
      default: 0
    },
    mountedRequestData: {
      type: Boolean,
      default: true
    },
    data: {
      type: Array,
      default() {
        return [];
      }
    },
    padding: {
      type: Array,
      default() {
        return ["8", "8", "8", "8"];
      }
    },
    borderRadius: {
      type: Array,
      default() {
        return ["0", "0"];
      }
    },
    topBottom: {
      type: String,
      default: ""
    },
    rowButtons: {
      type: Array,
      default() {
        return [];
      }
    },
    rowButtonsAlign: {
      type: String,
      default: "right"
    },
    rowButtonsMaxNumber: {
      type: Number,
      default: 2
    },
    rowButtonsMoreButtonStyle: {
      type: String,
      default: ""
    },
    rowButtonsMoreButtonText: {
      type: String,
      default: ""
    },
    rowClickToUrl: {
      type: String,
      default: ""
    },
    isButtonBar: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    areas() {
      let grid = this.matrix;
      let areas = [];
      if (grid) {
        _.each(grid, (el) => {
          _.each(el, (area) => {
            dsf.array.ensure(areas, area, (el2) => el2.name == area.name);
          });
        });
      }
      return areas;
    },
    $borderRadius() {
      return [this.pxToRem(this.borderRadius[0]), this.pxToRem(this.borderRadius[0]), this.pxToRem(this.borderRadius[1]), this.pxToRem(this.borderRadius[1])].join(" ");
    },
    $padding() {
      let padding = this.padding;
      return [this.pxToRem(padding[0]), this.pxToRem(padding[1]), this.pxToRem(padding[2]), this.pxToRem(padding[3])].join(" ");
    },
    splitWidthHorizontal() {
      if (this.topBottom == "split-small") {
        return "split-horizontal--8";
      } else if (this.topBottom == "split-medium") {
        return "split-horizontal--10";
      } else if (this.topBottom == "split-big") {
        return "split-horizontal--15";
      }
      return "";
    }
  },
  created() {},
  mounted() {},
  methods: {
    onLoad() {
      this.currentPage++;
      this.loadData();
    },
    rowFooterStyle() {
      if (!this.isDesign) {
        return {
          // "margin-top": this.pxToRem(-1 * this.padding[0]),
          "margin-right": this.pxToRem(-1 * this.padding[1]),
          "margin-bottom": this.pxToRem(-1 * this.padding[2]),
          "margin-left": this.pxToRem(-1 * this.padding[3])
        };
      }
    },
    onRefresh() {
      this.refreshing = true;
      this.finished = false;
      this.data.splice(0, this.data.length);
      this.loading = true;
      this.loadData();
    },
    getQueryInfo() {
      return {
        query: {},
        order: [{ code: "ds_create_time", sort: "asc" }]
      };
    },
    doLayout() {},
    getCellSetting(area) {
      if (this.contentLayout.setting) {
        if (this.contentLayout.setting[area.name]) {
          return this.contentLayout.setting[area.name];
        }
      }
      return {};
    },
    getCellFontColor(area) {
      let setting = this.getCellSetting(area);
      return setting.color;
    },
    getCellPadding(area) {
      let setting = this.getCellSetting(area);
      let padding = [this.pxToRem(setting.paddingTop || 0), this.pxToRem(setting.paddingRight || 0), this.pxToRem(setting.paddingBottom || 0), this.pxToRem(setting.paddingLeft || 0)];
      return padding.join(" ");
    },
    getCellClass(area) {
      let setting = this.getCellSetting(area);
      return [setting ? setting.fontSize : "ds-font5", setting ? setting.align : "left", setting ? setting.verticalAlign : "middle", setting ? (setting.bold == true ? "bold" : "") : ""];
    },
    getLineNum(area) {
      let setting = this.getCellSetting(area);
      if (setting && setting.lineNum) {
        return setting.lineNum;
      }
      return "auto";
    },
    getLineHeight(area) {
      let setting = this.getCellSetting(area);
      if (setting && setting.lineHeight) {
        return this.pxToRem(setting.lineHeight || "normal");
      }
      return "auto";
    },
    getCellHeight(area) {
      let result = "auto";
      let setting = this.getCellSetting(area);
      if (setting && setting.height) {
        result = this.pxToRem(setting.height || "auto");
      }
      if (this.isDesign && result == "auto") {
        return "100%";
      }
      return result;
    },
    loadData() {
      if (this.$vm) {
        if (this.refreshing) {
          this.currentPage = 1;
        }
        let queryInfo = this.getQueryInfo();
        let params = {
          pageNum: this.currentPage,
          pageSize: this.pageSize,
          query: JSON.stringify(queryInfo.query),
          order: JSON.stringify(queryInfo.order),
          namespace: this.$vm.nameSpace,
          pageName: this.$vm.pageName
        };
        let url = "/meta/list/data";
        dsf.mix(params, this.queryString);
        let listData = null;
        let hasError = false;
        return this.$http
          .get(url, params)
          .then(({ data }) => {
            if (data.state != 20000) {
              throw data;
            } else {
              listData = data.data;
              if (!this.refreshing) {
                //
                this.data.push(...listData);
              } else {
                //
                this.data.splice(0, this.data.length, ...listData);
              }
            }
          })
          .catch((ex) => {
            hasError = true;
            if (dsf.type(ex) == "string" || ex.message) {
              let msg = ex.message || ex;
              dsf.layer.message(msg, false);
            }
          })
          .finally(() => {
            if (hasError) {
              this.finished = true;
            }
            if (dsf.type(listData) == "array" && listData.length <= 0) {
              this.finished = true;
            }
            this.$nextTick(() => {
              this.loading = false;
              if (this.refreshing) {
                this.refreshing = false;
              }
            });
          });
      }
    },
    itemClickHandler(row, evt) {
      //row-clickrowClickToUrl
      if (this.$listeners["row-click"]) {
        this.$dispatch("row-click", row);
      } else {
        if (this.rowClickToUrl) {
          this.$router.push({
            path:this.rowClickToUrl
          });
        }
      }
    }
  },
  watch: {
    "contentLayout.grid": {
      handler(v) {
        if (this.isDesign) {
          createDefaultAreas.call(this);
          this.matrix = parseMatrix.call(this);
          createAreaSlots.call(this);
          let widths = this.contentLayout.widths.slice(0);
          let heights = this.contentLayout.heights.slice(0);
          dsf.array.removeAll(this.contentLayout.widths);
          dsf.array.removeAll(this.contentLayout.heights);
          for (let i = 0; i < this.matrix.length; i++) {
            if (!heights[i]) {
              this.contentLayout.heights[i] = "auto";
            } else {
              this.contentLayout.heights[i] = heights[i];
            }
            for (let k = 0; k < this.matrix[i].length; k++) {
              if (!widths[k]) {
                this.contentLayout.widths[k] = "auto";
              } else {
                this.contentLayout.widths[k] = widths[k];
              }
            }
          }
        } else {
          this.matrix = parseMatrix.call(this);
        }
      },
      immediate: true
    },
    "contentLayout.setting": {
      handler(v) {
        if (this.isDesign) {
          createDefaultAreas.call(this);
          this.matrix = parseMatrix.call(this);
          createAreaSlots.call(this);
          let widths = this.contentLayout.widths.slice(0);
          let heights = this.contentLayout.heights.slice(0);
          dsf.array.removeAll(this.contentLayout.widths);
          dsf.array.removeAll(this.contentLayout.heights);
          for (let i = 0; i < this.matrix.length; i++) {
            if (!heights[i]) {
              this.contentLayout.heights[i] = "auto";
            } else {
              this.contentLayout.heights[i] = heights[i];
            }
            for (let k = 0; k < this.matrix[i].length; k++) {
              if (!widths[k]) {
                this.contentLayout.widths[k] = "auto";
              } else {
                this.contentLayout.widths[k] = widths[k];
              }
            }
          }
        } else {
          this.matrix = parseMatrix.call(this);
        }
      }
    },
    data: {
      handler(v) {
        for (let i = 0; i < v.length; i++) {
          if (!("$editing" in v[i])) {
            v[i].$editing = false;
          }
          !("$editing" in v[i]) && this.$set(v[i], "$editing", false);
          !("$hash" in v[i]) && this.$set(v[i], "$hash", dsf.uuid());
          if ("$order" in v[i]) {
            this.$set(v[i], "$order", i);
          } else {
            v[i]["$order"] = i;
          }
          v[i].$index = i;
        }
      }
    }
  },
  design: {
    isMask: false,
    metadata: {
      isDeep: false,
      create(root) {
        let childMetaData = [];
        dsf.designer.recursionLayoutTree(this, null, null, (child) => {
          if (child != this) {
            let designOptions = child.$options.design || {};
            let metadataNode = designOptions.metadata || {};
            let result = metadataNode.create && metadataNode.create.call(child, root);
            if (result && result.metadata) {
              childMetaData.push(result);
              let isDeep = dsf.isDef(metadataNode.isDeep) ? metadataNode.isDeep : true;
              if (!isDeep) {
                return false;
              }
            }
          }
        });

        let query = {};
        let searchBox = [],
          attachSearch = [];
        for (let i = 0; i < this.searchInfo.length; i++) {
          let s = this.searchInfo[i];
          let item = null;
          if (s.type == "0") {
            item = {
              code: s.code,
              operator: s.operation,
              valueKey: "searchValue"
            };
            searchBox.push(item);
          } else {
            item = {
              code: s.code,
              operator: s.operation,
              valueKey: s.code
            };
            attachSearch.push(item);
            s.metadata_fullcode = item.code;
          }
        }
        //
        let seachBoxitem = { logic: "or", items: [] };
        seachBoxitem.items.push(...searchBox);

        //
        let attachSearchItem = { logic: "and", items: [] };
        attachSearchItem.items.push(...attachSearch);

        let searchitems = { logic: "and", items: [] };
        searchitems.items.push(seachBoxitem);
        searchitems.items.push(attachSearchItem);
        // seachBoxitem.items.length > 0 && searchitems.items.push(seachBoxitem);
        // attachSearchItem.items.length > 0 && searchitems.items.push(attachSearchItem);
        let superSearchItem = { logic: "and", items: [] };
        _.each(this.superSearchInfo, (el) => {
          let code = null;
          let operator = "";
          let valueKey = null;
          if (el.searchType == "enum") {
            let useFields = [];
            if (el?.children?.length > 0) {
              useFields = _.map(el.children, (f) => f.join == 1);
              useFields = useFields.slice(0, 1);
            } else {
              useFields = [el];
            }
            if (useFields.length == 1) {
              code = useFields[0].field;
              operator = useFields[0].operator;
              valueKey = useFields[0].field;
              if (operator == "in" || operator == "eq") {
                operator = "in";
              }
              let item = {
                code: code,
                operator: operator == "in" || operator == "eq" ? "in" : operator,
                valueKey: code
              };
              superSearchItem.items.push(item);
            }
          } else if (el.searchType == "text") {
            code = el.code;
            operator = el.operator;
            valueKey = el.code;
            superSearchItem.items.push({
              code: code,
              operator: operator,
              valueKey: valueKey
            });
          } else if (el.searchType == "datetime") {
            let useFields = [];
            if (el?.children?.length > 0) {
              useFields = _.map(el.children, (f) => f.join == 1);
              useFields = useFields.slice(0, 1);
            } else {
              useFields = [el];
            }
            if (useFields.length == 1) {
              superSearchItem.items.push({
                code: useFields[0].field,
                operator: useFields[0].operator,
                valueKey: useFields[0].field,
                dataType: "datetime"
              });
            }
          }
        });
        searchitems.items.push(superSearchItem);
        query = searchitems;
        let metadata = {
          query: query,
          browseScope: this.browseScope,
          _self: this
        };
        return [metadata, ...childMetaData];
      }
    },
    //
    contextMenuHandler(evt) {
      let target = findSelfCell.call(this, $(evt.target));
      if (target && target.length) {
        //
        let result = _.findIndex(this.rangeWrap.selected, (c) => {
          return c.gridIndex == ~~target.attr("index");
        });
        if (result < 0) {
          this.downCell = target;
          let cells = getRangeSelectedCells.call(this, this.downCell, null);
          updateRangeWrap.call(this, cells);
        }
        return [
          { text: "", hide: true, handler: mergeCellsHanlder },
          { text: "", disabled: false, handler: splitCellHandler },
          "-",
          { text: "()", disabled: false, handler: insertRowBefore },
          { text: "()", disabled: false, handler: insertRowAfter },
          { text: "()", disabled: false, handler: insertColumnBefore },
          { text: "()", disabled: false, handler: insertColumnAfter },
          "-",
          { text: "", disabled: false, handler: deleteRows },
          { text: "", disabled: false, handler: deleteColumns },
          "-",
          { text: "", disabled: false, handler: openCellSetting }
          // { text: "", disabled: false, handler: resetColWidth },
          // { text: "", disabled: false, handler: resetRowHeight }
        ];
      }
    },
    //
    clearContextMemuHandler() {
      this.isDown = false;
      clearRangeWrap.call(this);
    },
    //mousedown,mousedown
    clearMouseDownHandler() {
      this.isDown = false;
      clearRangeWrap.call(this);
    },
    //down
    mouseDownHandler(evt) {
      if (this.isDesign && evt.button === 0) {
        this.isDown = true;
        this.downCell = findSelfCell.call(this, $(evt.target));
        let cells = getRangeSelectedCells.call(this, this.downCell, null);
        updateRangeWrap.call(this, cells);
        evt.stopPropagation();
      }
    },
    //
    mouseMoveHandler(evt) {
      if (this.isDown) {
        let overCell = findSelfCell.call(this, $(evt.target));
        if (overCell) {
          if (this.downCell.get(0) != overCell.get(0)) {
            // debugger;
          }
          let cells = getRangeSelectedCells.call(this, this.downCell, overCell);
          updateRangeWrap.call(this, cells);
          evt.stopPropagation();
        }
      }
    },
    //up
    mouseUpHandler() {
      this.isDown = false;
    }
  }
});

function parseMatrix() {
  let matrix = [];
  let rows = 0,
    cols = 0;
  let grid = this.contentLayout.grid;
  for (let i = 0; i < grid.length; i++) {
    if (!matrix[i]) {
      matrix[i] = [];
    }
    for (let k = 0; k < grid[i].length; k++) {
      matrix[i][k] = null;
      if (!this.contentLayout.widths[k]) {
        this.contentLayout.widths[k] = "auto";
      }
    }
  }
  let map = {};
  for (let i = 0; i < grid.length; i++) {
    let areas = grid[i];
    for (let k = 0; k < areas.length; k++) {
      let areaName = areas[k];
      if (!map[areaName]) {
        map[areaName] = {
          areaName: GRID_AREA_KEY + areaName,
          name: areaName,
          rowIndex: i,
          colIndex: k,
          rowSpan: 1,
          colSpan: 1
        };
      } else {
        let rowSpan = Math.abs(i - map[areaName].rowIndex) + 1;
        let colSpan = Math.abs(k - map[areaName].colIndex) + 1;
        map[areaName].rowSpan = rowSpan;
        map[areaName].colSpan = colSpan;
      }
      map[areaName].setting = {
        padding: this.getCellPadding(map[areaName]),
        height: this.getCellHeight(map[areaName]),
        lineHeight: this.getLineHeight(map[areaName]),
        color: this.getCellFontColor(map[areaName]),
        class: this.getCellClass(map[areaName]),
        lineNum: this.getLineNum(map[areaName])
      };
      let area = map[areaName];
      matrix[i][k] = area;
    }
  }
  return matrix;
}

function createDefaultAreas() {
  let c = this.cellSerial;
  if (this.contentLayout.grid.length <= 0) {
    for (let i = 0; i < 3; i++) {
      let s = [];
      for (let k = 0; k < 4; k++) {
        c++;
        let area = dsf.math.numToEnglishLetter(c);
        s.push(area);
      }
      this.contentLayout.grid.push(s);
    }
  }
  this.$emit("update:cellSerial", c);
  // this.cellSerial = c;
}

function createAreaSlots() {
  let slots = this.slots.slice(0);
  dsf.array.removeWith(this.slots, (el) => el.name.indexOf(GRID_AREA_KEY) == 0);
  if (this.matrix) {
    for (let i = 0; i < this.matrix.length; i++) {
      for (let k = 0; k < this.matrix[i].length; k++) {
        let area = this.matrix[i][k];
        let areaSlot = _.find(this.slots, (el) => el.name == area.name);
        if (!areaSlot) {
          areaSlot = {
            name: area.name,
            controls: [],
            scope: "scope",
            dataKey: "row",
            repeat: true,
            textProxy: true
          };
          this.slots.push(areaSlot);
        }
        areaSlot.controls = areaSlot ? areaSlot.controls : [];
      }
    }
  }
}

function getAreaSlots() {
  let slots = _.filter(this.slots, (el) => el.name.indexOf("grid-area-") == 0);
  return slots;
}

function matrixToGrid(matrix) {
  let grid = [];
  matrix = matrix || this.matrix;
  _.each(matrix, (el) => {
    let row = [];
    _.each(el, (sub) => {
      row.push(sub.name.replace(GRID_AREA_KEY, ""));
    });
    grid.push(row);
  });
  return grid;
}

/**
 * 
 */
function updateRangeWrap(cells) {
  if (!this.isDesign) {
    return;
  }
  if (!cells || !cells.length) {
    this.rangeWrap.show = false;
    return;
  }
  let top = [];
  let left = [];
  let bottom = [];
  let right = [];
  let selected = cells.map((ce) => {
    const cePosition = ce.position();
    if (cePosition) {
      top.push(cePosition.top);
      left.push(cePosition.left);
      bottom.push(cePosition.top + ce.height());
      right.push(cePosition.left + ce.width());
      let index = ~~ce.attr("index");
      let rowIndex = ~~ce.attr("row-index");
      let colIndex = ~~ce.attr("col-index");
      let name = ce.attr("name");
      return {
        areaName: GRID_AREA_KEY + name,
        name: name,
        gridIndex: index,
        rowIndex: ~~rowIndex,
        colIndex: ~~colIndex
      };
    }
  });
  top = Math.min(...top);
  left = Math.min(...left);
  bottom = Math.max(...bottom);
  right = Math.max(...right);
  this.rangeWrap = {
    show: true,
    width: right - left,
    height: bottom - top - 1,
    top: top - 4,
    left: left - 4,
    selected
  };
}
/**
 * 
 * 
 */
function getRangeSelectedCells(start, end) {
  if (end && start.get(0) != end.get(0)) {
    let { minRowIndex, maxRowIndex, minColIndex, maxColIndex } = getRangeRect.call(this, start, end);
    return getRangeCells.call(this, minRowIndex, maxRowIndex, minColIndex, maxColIndex);
  } else {
    if (start) {
      return [$(start)];
    }
    return [];
  }
}

/**
 * 
 * 
 */
function getRangeRect(start, end) {
  let endRowIndex = ~~end.attr("row-index");
  let endColIndex = ~~end.attr("col-index");
  let endRowspan = ~~end.attr("rowspan") - 1;
  let endColspan = ~~end.attr("colspan") - 1;
  let startRowIndex = ~~start.attr("row-index");
  let startColIndex = ~~start.attr("col-index");
  let startRowspan = ~~start.attr("rowspan") - 1;
  let startColspan = ~~start.attr("colspan") - 1;
  let minRowIndex = Math.min(startRowIndex, endRowIndex);
  let maxRowIndex = Math.max(startRowIndex + startRowspan, endRowIndex + endRowspan);
  let minColIndex = Math.min(startColIndex, endColIndex);
  let maxColIndex = Math.max(startColIndex + startColspan, endColIndex + endColspan);
  return checkRange.call(this, minRowIndex, maxRowIndex, minColIndex, maxColIndex);
}

/**
 * 
 * # 
 * # 
 */
function getRangeCells(minRowIndex, maxRowIndex, minColIndex, maxColIndex) {
  let selected = [];
  for (let i = minRowIndex; i <= maxRowIndex; i++) {
    for (let j = minColIndex; j <= maxColIndex; j++) {
      let $el = getCellByIndex.call(this, i, j);
      if ($el) {
        selected.push($el);
      }
    }
  }
  return selected;
}

/**
 * 
 */
function checkRange(minRowIndex2, maxRowIndex2, minColIndex2, maxColIndex2) {
  let cells = getRangeCells.call(this, minRowIndex2, maxRowIndex2, minColIndex2, maxColIndex2);
  let endRowIndexs = [],
    endColIndexs = [],
    startRowIndexs = [],
    startColIndexs = [];
  cells.forEach((ce) => {
    startRowIndexs.push(+ce.attr("row-index"));
    startColIndexs.push(+ce.attr("col-index"));
    endRowIndexs.push(+ce.attr("row-index") + +ce.attr("rowspan") - 1);
    endColIndexs.push(+ce.attr("col-index") + +ce.attr("colspan") - 1);
  });
  let minRowIndex = Math.min(...startRowIndexs);
  let maxRowIndex = Math.max(...endRowIndexs);
  let minColIndex = Math.min(...startColIndexs);
  let maxColIndex = Math.max(...endColIndexs);
  if (minRowIndex == minRowIndex2 && maxRowIndex == maxRowIndex2 && minColIndex == minColIndex2 && maxColIndex == maxColIndex2) {
    return { minRowIndex, maxRowIndex, minColIndex, maxColIndex };
  } else {
    return checkRange.call(this, minRowIndex, maxRowIndex, minColIndex, maxColIndex);
  }
}

/**
 * 
 */
function getCellByIndex(rowIndex, colIndex) {
  // let name = GRID_AREA_KEY + rowIndex + "-" + colIndex;
  let area = this.matrix[rowIndex][colIndex];
  if (area) {
    let name = GRID_AREA_KEY + area.name;
    let el = this.$el.querySelector("." + name);
    return $(el);
  }
  return null;
}

/**
 * 
 */
function getCellListByIndexs(indexs) {
  let cells = [];
  _.each(indexs, (el) => {
    let name = GRID_AREA_KEY + el.name;
    let cell = this.$el.querySelector("." + name);
    if (cell) {
      cells.push($(cell));
    }
  });
  // indexs.forEach(({ rowIndex, colIndex }) => {
  //   let cell = getCellByIndex.call(this, rowIndex, colIndex);
  //   if (cell) {
  //     cells.push(cell);
  //   }
  // });
  return cells;
}

/**
 * 
 */
function clearRangeWrap() {
  this.downCell = null;
  this.rangeWrap = {
    show: false,
    width: 0,
    height: 0,
    top: -10,
    left: -10,
    selected: []
  };
}

// 
function findSelfCell(cells) {
  for (let i = 0; i < cells.length; i++) {
    let cell = cells.eq(i);
    if (!cell.hasClass("grid-area")) {
      cell = cell.closest(".grid-area");
    }
    let table = cell.closest(".ds-datagrid");
    if (this.$el === table.get(0)) {
      return cell;
    }
  }
  return null;
}

//
function mergeCells(startRowIndex, endRowIndex, startColIndex, endColIndex) {
  // let layout = JSON.parse(JSON.stringify(this.contentLayout));
  let matrix = dsf.mix(true, [], this.matrix);
  let area = this.matrix[startRowIndex][startColIndex];
  if (area) {
    for (let i = startRowIndex; i <= endRowIndex; i++) {
      for (let j = startColIndex; j <= endColIndex; j++) {
        matrix[i][j] = area;
      }
    }
  }
  let layout = matrixToGrid.call(this, matrix);
  this.contentLayout.grid = layout;
  // this.contentLayout.grid
  // this.$emit('update:layout', layout);
  // 
  this.$nextTick(() => {
    let selectCells = getCellListByIndexs.call(this, this.rangeWrap.selected);
    updateRangeWrap.call(this, selectCells);
  });
}

// 
function getMergedCells(cells) {
  let arr = [];
  cells.forEach((el) => {
    let v = el.get ? el : $(el);
    let rowspan = +v.attr("rowspan");
    let colspan = +v.attr("colspan");
    if (rowspan > 1 || colspan > 1) {
      arr.push(v);
    }
  });
  return arr;
}

//
function splitCell(cells) {
  let matrix = dsf.mix(true, [], this.matrix);
  // let layout = JSON.parse(JSON.stringify(this.contentLayout));
  let c = this.cellSerial;
  cells.forEach((cell) => {
    let rowIndex = ~~cell.attr("row-index");
    let colIndex = ~~cell.attr("col-index");
    let colspan = ~~cell.attr("colspan") - 1;
    let rowspan = ~~cell.attr("rowspan") - 1;
    let name = cell.attr("name");
    for (let i = rowIndex, l = rowIndex + rowspan; i <= l; i++) {
      for (let j = colIndex, m = colIndex + colspan; j <= m; j++) {
        // matrix[i][j] = i * layout.widths.length + j + 1;
        if (i == rowIndex && j == colIndex) {
          matrix[i][j] = { name: name };
        } else {
          matrix[i][j] = { name: dsf.math.numToEnglishLetter(++c) };
        }
      }
    }
  });
  this.$emit("update:cellSerial", c);
  let layout = matrixToGrid.call(this, matrix);
  this.contentLayout.grid = layout;
  // 
  this.$nextTick(() => {
    let selectCells = getCellListByIndexs.call(this, this.rangeWrap.selected);
    updateRangeWrap.call(this, selectCells);
  });
}

// rowscols
function getTableRect(rows, cols) {
  return Array(rows)
    .fill(1)
    .map((v, r) => {
      return Array(cols)
        .fill(1)
        .map((v, c) => {
          return c + 1 + r * cols;
        });
    });
}

// rowIndex
function addRow(rowIndex, pos) {
  let matrix = dsf.mix(true, [], this.matrix);
  let newRow = [],
    colNum = 0;
  let currentRow = matrix[rowIndex];
  for (let i = 0; i < currentRow.length; i++) {
    colNum++;
  }
  let c = this.cellSerial;
  for (let i = 0; i < colNum; i++) {
    let cell = currentRow[i];
    let s = cell.rowIndex;
    let e = s + cell.rowSpan - 1;
    let newCell = {};
    if (s < rowIndex && e >= rowIndex) {
      newCell.name = cell.name;
    } else {
      newCell.name = dsf.math.numToEnglishLetter(++c);
    }
    newRow.push(newCell);
  }
  matrix.splice(pos == "before" ? rowIndex : rowIndex + 1, 0, newRow);
  let layout = matrixToGrid.call(this, matrix);
  this.contentLayout.grid = layout;
  this.$emit("update:cellSerial", c);
  // 
  this.$nextTick(() => {
    let selectCells = getCellListByIndexs.call(this, this.rangeWrap.selected);
    updateRangeWrap.call(this, selectCells);
  });
}
// colIndex
function addCol(colIndex, pos) {
  let matrix = dsf.mix(true, [], this.matrix);
  let c = this.cellSerial;
  for (let i = 0; i < matrix.length; i++) {
    let row = matrix[i];
    let newCell = {};
    let cell = row[colIndex];
    let s = cell.colIndex;
    let e = s + cell.colSpan - 1;
    if (s < colIndex && e >= colIndex) {
      newCell.name = cell.name;
    } else {
      newCell.name = dsf.math.numToEnglishLetter(++c);
    }
    row.splice(pos == "before" ? colIndex : colIndex + 1, 0, newCell);
  }
  let layout = matrixToGrid.call(this, matrix);
  this.contentLayout.grid = layout;
  this.$emit("update:cellSerial", c);
  // 
  this.$nextTick(() => {
    let selectCells = getCellListByIndexs.call(this, this.rangeWrap.selected);
    updateRangeWrap.call(this, selectCells);
  });
}

/************************/
//
function mergeCellsHanlder() {
  let cells = getCellListByIndexs.call(this, this.rangeWrap.selected);
  let start = cells[0];
  let end = cells[cells.length - 1];
  let endRowIndex = ~~end.attr("row-index");
  let endColIndex = ~~end.attr("col-index");
  let rowspan = ~~end.attr("rowspan");
  let colspan = ~~end.attr("colspan");
  let startRowIndex = ~~start.attr("row-index");
  let startColIndex = ~~start.attr("col-index");
  mergeCells.call(this, startRowIndex, endRowIndex + rowspan - 1, startColIndex, endColIndex + colspan - 1);
}

//
function openCellSetting() {
  let els = getCellListByIndexs.call(this, this.rangeWrap.selected);
  let cells = [];
  _.each(els, (el, i) => {
    let rIndex = ~~el.attr("row-index");
    let cIndex = ~~el.attr("col-index");
    let cell = this.matrix[rIndex][cIndex];
    cells.push(cell);
  });
  let _this = this;
  this.$openDialog({
    title: "",
    content: "DesGridLayoutCellSetting",
    width: "40vw",
    height: "60vh",
    params: {
      cells,
      owner: _this
    },
    btns: [
      {
        text: "",
        handler(v) {
          let result = v.yes();
          if (!_this.contentLayout.setting) {
            _this.$set(_this.contentLayout, "setting", {});
          }
          _.each(result, (v, k) => {
            _this.$set(_this.contentLayout.setting, k, v);
          });
          let layout = matrixToGrid.call(_this);
          _this.contentLayout.grid = layout;
          // 
          _this.$nextTick(() => {
            let selectCells = getCellListByIndexs.call(_this, _this.rangeWrap.selected);
            updateRangeWrap.call(_this, selectCells);
          });
        }
      },
      {
        text: ""
      }
    ]
  });
}

//
function splitCellHandler() {
  let selectCells = getCellListByIndexs.call(this, this.rangeWrap.selected);
  let cells = getMergedCells.call(this, selectCells);
  splitCell.call(this, cells);
}
//
function insertRowBefore() {
  let cells = getCellListByIndexs.call(this, this.rangeWrap.selected);
  let cell = cells[0];
  let rowIndex = ~~cell.attr("row-index");
  addRow.call(this, rowIndex, "before");
}
//
function insertRowAfter() {
  let cells = getCellListByIndexs.call(this, this.rangeWrap.selected);
  let cell = cells[cells.length - 1];
  let rowIndex = ~~cell.attr("row-index") + ~~cell.attr("rowspan") - 1;
  addRow.call(this, rowIndex, "after");
}
//
function insertColumnBefore() {
  let cells = getCellListByIndexs.call(this, this.rangeWrap.selected);
  let cell = cells[0];
  let colIndex = ~~cell.attr("col-index");
  addCol.call(this, colIndex, "before");
}
//
function insertColumnAfter() {
  let cells = getCellListByIndexs.call(this, this.rangeWrap.selected);
  let cell = cells[cells.length - 1];
  let colIndex = ~~cell.attr("col-index") + ~~cell.attr("colspan") - 1;
  addCol.call(this, colIndex, "after");
}
//
function deleteRows() {
  let cells = getCellListByIndexs.call(this, this.rangeWrap.selected);
  let start = cells[0];
  let end = cells[cells.length - 1];
  let minRowIndex = ~~start.attr("row-index");
  let maxRowIndex = ~~end.attr("row-index") + ~~end.attr("rowspan") - 1;
  let deleteNum = maxRowIndex - minRowIndex + 1;
  // let layout = JSON.parse(JSON.stringify(this.contentLayout));
  // layout.matrix.splice(minRowIndex, deleteNum);
  // layout.heights.splice(minRowIndex, deleteNum);
  // this.$emit('update:layout', layout);
  let matrix = dsf.mix(true, [], this.matrix);
  matrix.splice(minRowIndex, deleteNum);
  let layout = matrixToGrid.call(this, matrix);
  this.contentLayout.grid = layout;
  // 
  this.$nextTick(() => {
    let selectCells = getCellListByIndexs.call(this, this.rangeWrap.selected);
    updateRangeWrap.call(this, selectCells);
  });
}
//
function deleteColumns() {
  let cells = getCellListByIndexs.call(this, this.rangeWrap.selected);
  let start = cells[0];
  let end = cells[cells.length - 1];
  let minColIndex = +start.attr("col-index");
  let maxColIndex = +end.attr("col-index") + +end.attr("colspan") - 1;
  let deleteNum = maxColIndex - minColIndex + 1;
  let matrix = dsf.mix(true, [], this.matrix);
  for (let i = 0; i < matrix.length; i++) {
    matrix[i].splice(minColIndex, deleteNum);
  }
  let layout = matrixToGrid.call(this, matrix);
  this.contentLayout.grid = layout;
  // 
  this.$nextTick(() => {
    let selectCells = getCellListByIndexs.call(this, this.rangeWrap.selected);
    updateRangeWrap.call(this, selectCells);
  });
  // let layout = JSON.parse(JSON.stringify(this.contentLayout));
  // this.contentLayout.matrix.forEach((rows, index) => {
  //   layout.matrix[index].splice(minColIndex, deleteNum);
  // });
  // layout.widths = new Array(layout.matrix[0].length).fill(
  //   dsf.pxToRem(+(100 / layout.matrix[0].length).toFixed(3) + "%")
  // );
  // this.$emit('update:layout', layout);
}
/************************/
</script>